import { observable, action, computed } from 'mobx';
import * as api from '../lib/api';
import cheerio from 'cheerio';

export default class goldStore {
  @observable date = new Date();
  @observable week = []; //년-월-일
  @observable weekNotYear = []; //월-일
  @observable buySise = []; //살때 시세
  @observable sellSise = []; //팔때 시세
  @observable calculatorToggle = false; //그람 계산기 토글
  @observable buyGoldValue = null; //그람 계산기 인풋 벨류
  @observable todayBuySise = null;
  @observable todaySellSise = null;

  constructor(root) {
    this.root = root;

    this.actGoldList();
    this.todayDate();
  }

  @action
  actGoldList = async () => {
    try {
      this.root.common.loading = true;

      let buyGold = [];
      let buyGoldTemp = [];
      let buyImgGold = [];
      let buyImgGoldTemp = [];
      let sellGold = [];
      const response = await api.goldSise();
      const $ = cheerio.load(response.data, { _useHtmlParser2: true });

      const $buyList = $('.mb_70 >div >table +table >tr +tr >td');
      $buyList.each(function(i, elem) {
        buyGold[i] = {
          buyGold: $(this)
            .find('span.ctype_blackb')
            .text(),
          buyPrep: $(this)
            .find('span.ctype_grayb')
            .text()
        };
      });

      $buyList.find('table td img').each(function(i, elem) {
        if ($(this).attr('alt') === '') {
          return;
        } else {
          buyImgGold[i] = $(this)
            .attr('src')
            .split('/')[3]
            .split('_')[1]
            .split('.')[0];
        }
      });

      buyGoldTemp = buyGold.splice(1);
      buyImgGoldTemp = buyImgGold.filter(n => true);
      const buyGoldSise = buyGoldTemp.map((info, index) => {
        return (info = { ...info, fluctuation: buyImgGoldTemp[index] });
      });

      const $sellList = $('.mt_10 >table >tr +tr >td table td .ctype_blackb');
      $sellList.each(function(i, elem) {
        sellGold[i] = {
          gold24: $(this).text(),
          gold18: $(this)
            .parent()
            .parent()
            .next()
            .next()
            .find('>td')
            .text(),
          gold14: $(this)
            .parent()
            .parent()
            .next()
            .next()
            .next()
            .next()
            .find('>td')
            .text()
        };
      });

      this.todayBuySise = parseInt(buyGoldSise[0].buyGold.replace(/,/g, ''));
      this.todaySellSise = parseInt(sellGold[0].gold24.replace(/,/g, ''));
      this.buySise = buyGoldSise;
      this.sellSise = sellGold;
      this.root.common.loading = false;
    } catch (e) {
      console.log(e);
    }
  };

  @action
  todayDate = () => {
    const date = this.date;
    let weekTemp = [];
    let weekNotYearTemp = [];

    for (var i = 7; i >= 0; i--) {
      let tmp = date.getDay();
      if (tmp === 0) {
      } else {
        weekTemp.push(date.toISOString().substr(0, 10));
        weekNotYearTemp.push(date.toISOString().substr(5, 5));
      }
      let dateChange = date.getTime() - 1 * 1000 * 60 * 60 * 24;
      date.setTime(dateChange);
    }
    this.week = weekTemp;
    this.weekNotYear = weekNotYearTemp;
  };

  @action
  actGoldToggleTrue = () => {
    this.calculatorToggle = true;
  };
  @action
  actGoldToggleFalse = () => {
    this.calculatorToggle = false;
  };
  @action
  actGoldValue = value => {
    value ? (this.buyGoldValue = parseInt(value)) : (this.buyGoldValue = 0);
  };

  @computed
  get buyTotal() {
    return this.buyGoldValue * this.todayBuySise;
  }
  @computed
  get sellTotal() {
    return this.buyGoldValue * this.todaySellSise;
  }
}

